              {{ voting.voting_type === 'single' ? 'ÂñÆÈÅ∏Âà∂' : 'Â§öÈÅ∏Âà∂' }}
            </span>
          </div>
          <div class="info-item">
            <span class="info-label">üìà ÊäïÁ•®ÁõÆÁöÑ</span>
