          </div>
        </div>

        <div v-if="voting.attachments && voting.attachments.length > 0" class="attachments">
          <h3>üìé ÈôÑ‰ª∂Ë≥áÊñô</h3>
          <ul class="attachment-list">
            <li v-for="(attachment, index) in voting.attachments" :key="index">
              <a :href="attachment.url" target="_blank" class="attachment-link">
                {{ attachment.name }}
              </a>
            </li>
          </ul>
        </div>
      </div>

      <div v-if="!hasVoted && voting.status === 'ongoing'" class="voting-options">
        <h2 class="section-title">üîò Ë´ãÈÅ∏ÊìáÊÇ®ÁöÑÊäïÁ•®ÈÅ∏È†Ö</h2>
        <div class="options-list">
          <label 
            v-for="option in voting.voting_options" 
            :key="option.id"
            class="option-card"
            :class="{ selected: selectedOption === option.id }"
          >
            <input 
              type="radio" 
              :name="`voting-${voting.id}`"
              :value="option.id"
              v-model="selectedOption"
              class="option-radio"
            />
            <div class="option-content">
              <h4 class="option-title">{{ option.option_text }}</h4>
              <p v-if="option.description" class="option-description">
                {{ option.description }}
              </p>
            </div>
          </label>
        </div>

        <div class="voting-actions">
          <button 
            class="btn-submit"
            :disabled="!selectedOption || submitting"
            @click="submitVote"
          >
            {{ submitting ? 'Êèê‰∫§‰∏≠...' : 'Á¢∫Ë™çÊäïÁ•®' }}
          </button>
        </div>
      </div>

      <div v-if="hasVoted || voting.status === 'ended'" class="voting-results">
        <h2 class="section-title">üìä ÊäïÁ•®ÁµêÊûú</h2>
        
        <!-- ‰ΩøÁî®ÈÄ≤ÈöéÂúñË°®ÁµÑ‰ª∂ -->
        <VotingChart 
          :results="results"
          :statistics="statistics"
        />
      </div>

      <div class="voting-notice">
        <h3>üì£ ÂÇôË®ª</h3>
        <ul>
          <li v-if="voting.status === 'ongoing'">
            ÊäïÁ•®ÁµêÊûúÂ∞áÊñº {{ formatFullDate(voting.end_date) }} ÂæåÂÖ¨Âëä
          </li>
          <li>Ëã•ÁÑ°Ê≥ïÁ∑ö‰∏äÊäïÁ•®ÔºåÂèØËá≥ÁÆ°ÁêÜÂÆ§Â°´ÂØ´Á¥ôÊú¨ÈÅ∏Á•®</li>
          <li>ÊäïÁ•®Êé°ÂåøÂêçÂà∂ÔºåÊÇ®ÁöÑÂÄã‰∫∫Ë≥áË®äÂ∞áË¢´‰øùÂØÜ</li>
        </ul>
      </div>
    </template>

    <div v-if="showConfirmModal" class="modal-overlay" @click="showConfirmModal = false">
      <div class="modal-content" @click.stop>
        <h3 class="modal-title">Á¢∫Ë™çÊäïÁ•®</h3>
        <p class="modal-message">
          ÊÇ®ÈÅ∏ÊìáÁöÑÊòØÔºö<strong>{{ getSelectedOptionText() }}</strong>
        </p>
        <p class="modal-warning">ÊäïÁ•®ÂæåÁÑ°Ê≥ïÊõ¥ÊîπÔºåË´ãÁ¢∫Ë™çÊÇ®ÁöÑÈÅ∏Êìá</p>
        <div class="modal-actions">
          <button class="btn-cancel" @click="showConfirmModal = false">ÂèñÊ∂à</button>
          <button class="btn-confirm" @click="confirmVote">Á¢∫Ë™çÊäïÁ•®</button>
        </div>
      </div>
    </div>

    <div v-if="showSuccessMessage" class="success-message">
      <span class="success-icon">‚úÖ</span>
      <span>ÊäïÁ•®ÊàêÂäüÔºÅÊÑüË¨ùÊÇ®ÁöÑÂèÉËàá</span>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { votingApi, supabase } from '../utils/supabase';
import { useStore } from 'vuex';
import VotingChart from './VotingChart.vue';

export default {
  name: 'VotingDetail',
  components: {
    VotingChart
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    
    const loading = ref(true);
    const voting = ref(null);
    const selectedOption = ref(null);
    const hasVoted = ref(false);
    const userVote = ref(null);
    const submitting = ref(false);
    const showConfirmModal = ref(false);
    const showSuccessMessage = ref(false);
    const results = ref([]);
    const statistics = ref({
      totalVotes: 0,
      participationRate: '0',
      floorDistribution: null
    });
    
    let votingSubscription = null;

    const sortedResults = computed(() => {
      return [...results.value].sort((a, b) => b.vote_count - a.vote_count);
    });

    const getStatusLabel = (status) => {
      const statusMap = {
        'upcoming': 'Âç≥Â∞áÈñãÂßã',
        'ongoing': 'ÈÄ≤Ë°å‰∏≠',
        'ended': 'Â∑≤ÁµêÊùü'
      };
      return statusMap[status] || status;
    };

    const formatFullDate = (dateString) => {
      const date = new Date(dateString);
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
    };

    const getBarColor = (percentage) => {
      if (percentage >= 50) return 'linear-gradient(135deg, #10b981, #059669)';
      if (percentage >= 30) return 'linear-gradient(135deg, #667eea, #764ba2)';
      return 'linear-gradient(135deg, #f59e0b, #d97706)';
    };

    const getSelectedOptionText = () => {
      const option = voting.value?.voting_options?.find(o => o.id === selectedOption.value);
      return option?.option_text || '';
    };

    const goBack = () => {
      router.push('/voting');
    };

    const fetchVotingDetail = async () => {
      try {
        loading.value = true;
        const topicId = route.params.id;
        
        const topic = await votingApi.getVotingTopic(topicId);
        
        const now = new Date();
        if (new Date(topic.start_date) > now) {
          topic.status = 'upcoming';
        } else if (new Date(topic.end_date) < now) {
          topic.status = 'ended';
        } else {
          topic.status = 'ongoing';
        }
        
        voting.value = topic;
        
        const userId = store.state.user?.id;
        if (userId) {
          const vote = await votingApi.checkUserVoted(topicId, userId);
          if (vote) {
            hasVoted.value = true;
            userVote.value = vote;
          }
        }
        
        if (hasVoted.value || topic.status === 'ended') {
          await fetchResults();
        }
        
        if (topic.status === 'ongoing') {
          subscribeToUpdates(topicId);
        }
      } catch (error) {
        console.error('Error fetching voting detail:', error);
      } finally {
        loading.value = false;
      }
    };

    const fetchResults = async () => {
      try {
        const topicId = route.params.id;
        const [resultsData, statsData] = await Promise.all([
          votingApi.getVotingResults(topicId),
          votingApi.getVotingStatistics(topicId)
        ]);
        
        results.value = resultsData;
        statistics.value = statsData;
      } catch (error) {
        console.error('Error fetching results:', error);
      }
    };

    const submitVote = () => {
      if (!selectedOption.value) return;
      showConfirmModal.value = true;
    };

    const confirmVote = async () => {
      try {
        submitting.value = true;
        showConfirmModal.value = false;
        
        const topicId = route.params.id;
        const userId = store.state.user?.id;
        console.log('ÊäïÁ•®Ê™¢Êü• - Áî®Êà∂ID:', userId);
        console.log('ÊäïÁ•®Ê™¢Êü• - Ë™çË≠âÁãÄÊÖã:', store.state.isAuthenticated);
        
        if (!userId || !store.state.isAuthenticated) {
          alert('Ë´ãÂÖàÁôªÂÖ•ÂæåÂÜçÊäïÁ•®');
          const returnUrl = route.fullPath;
          console.log('Ë∑≥ËΩâÂà∞ÁôªÂÖ•È†ÅÈù¢ÔºåËøîÂõûURL:', returnUrl);
          console.log('ÂÆåÊï¥Ë∑ØÁî±Ë≥áË®ä:', route);
          
          router.push({ 
            path: '/login', 
            query: { 
              returnTo: returnUrl,
              from: 'voting'
            } 
          });
          return;
        }
        
        const additionalData = {
          floor_number: store.state.user?.floor_number,
          unit_number: store.state.user?.unit_number
        };
        
        console.log('Ê∫ñÂÇôÊèê‰∫§ÊäïÁ•®:', { topicId, selectedOption: selectedOption.value, userId, additionalData });
        
        // Áõ¥Êé•ÊèíÂÖ•ÊäïÁ•®Ë®òÈåÑÔºåÁ∞°Âåñ API Ë™øÁî®
        const { data, error } = await supabase
          .from('voting_records')
          .insert([{
            topic_id: topicId,
            user_id: userId,
            option_id: selectedOption.value,
            floor_number: additionalData.floor_number,
            unit_number: additionalData.unit_number,
            user_agent: navigator.userAgent
          }])
          .select()
          .single();

        if (error) {
          console.error('Supabase insert error:', error);
          throw error;
        }

        console.log('ÊäïÁ•®ÊàêÂäü:', data);
        
        hasVoted.value = true;
        userVote.value = data;
        showSuccessMessage.value = true;
        
        // ÂòóË©¶Êõ¥Êñ∞ÁµêÊûú
        try {
          await fetchResults();
        } catch (resultError) {
          console.warn('Êõ¥Êñ∞ÁµêÊûúÂ§±ÊïóÔºå‰ΩÜÊäïÁ•®ÊàêÂäü:', resultError);
        }
        
        setTimeout(() => {
          showSuccessMessage.value = false;
        }, 3000);
      } catch (error) {
        console.error('Error submitting vote:', error);
        console.error('Error details:', error);
        
        let errorMessage = 'ÊäïÁ•®Â§±ÊïóÔºåË´ãÁ®çÂæåÂÜçË©¶';
        if (error.code === '23505') {
          errorMessage = 'ÊÇ®Â∑≤Á∂ìÊäïÈÅéÁ•®‰∫ÜÔºÅ';
        } else if (error.message) {
          errorMessage = `ÊäïÁ•®Â§±ÊïóÔºö${error.message}`;
        }
        
        alert(errorMessage);
      } finally {
        submitting.value = false;
      }
    };

    const subscribeToUpdates = (topicId) => {
      votingSubscription = votingApi.subscribeToVotingUpdates(topicId, async (payload) => {
        console.log('Voting update:', payload);
        await fetchResults();
      });
    };

    onMounted(() => {
      fetchVotingDetail();
    });

    onUnmounted(() => {
      if (votingSubscription) {
        votingApi.unsubscribeFromVotingUpdates(votingSubscription);
      }
    });

    return {
      loading,
      voting,
      selectedOption,
      hasVoted,
      userVote,
      submitting,
      showConfirmModal,
      showSuccessMessage,
      results,
      sortedResults,
      statistics,
      getStatusLabel,
      formatFullDate,
      getBarColor,
      getSelectedOptionText,
      goBack,
      submitVote,
      confirmVote
    };
  }
};
</script>

